html,
body {
    margin: 0;
    width: 100%;
    height: 100%;
}

body {
    background-color: #60605D;
    margin: 0 auto;
    padding: 0;
    font-family: 'Crimson Text', serif
}

h2 {
    font-family: 'Crimson Text', serif;
    color: #B4BD89;
    text-align: center;
}

#root {
    position: absolute;
    height: 100%;
    width: 100%;
}

#headerTitle {
    color: #960015;
    font-family: 'Courgette', cursive;
    width: 100%;
    text-align: center;
}

#appBox {
    border: 2px solid black;
    background-color: #E2E2DE;
    display: grid;
    grid-template-rows: 100%;
    grid-template-areas: "left-pane recipes";
}

#recipes-body {
    grid-area: recipes;
    position: relative;
}

#recipeContainer{
    position: realtive;
    width: 100%;
    height: 100%;
}

#left-pane {
    grid-area: left-pane;
}

hr {
    border: solid 3px #960015;
    width: 95%;
}

button {
    cursor: pointer;
    font-family: 'Crimson Text', serif;
}

@media only screen and (max-width: 768px) {
    #headerTitle {
        position: relative;
        height: 8%;
    }

    #appBox {
        position: relative;
        width: 99%;
        height: 85%;
        grid-template-columns: 25% 75%;
    }

    button {
        font-size: 3.5vw;
    }

    hr {
        display: none;
    }

    h4 {
        text-align: center;
        position: absolute;
        width: 100%;
        top: 20%;
    }

    textarea {
        position: relative;
        width: 97%;
    }

}

@media only screen and (min-width: 768px) {
    #headerTitle {
        position: relative;
        height: 10%;
    }

    #appBox {
        position: relative;
        margin: auto;
        left: 0;
        bottom: 0;
        right: 0;
        width: 60%;
        height: 80%;
        border: 2px solid black;
        grid-template-columns: 20% 80%;
    }

    button {
        font-size: 1.5vw;
        text-align: center;
        width: 100%;
        height: 50px;
    }
}


@media only screen and (min-width: 1000px) {
    #headerTitle {
        position: absolute;
        top: 80px;
    }

    #appBox {
        position: absolute;
        margin: auto;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
        width: 900px;
        height: 600px;
        border: 2px solid black;
        grid-template-columns: 150px 750px;
    }

    h4 {
        position: absolute;
        right: 125px;
        top: 150px;
    }

    button {
        font-size: 1vw;
        text-align: center;
        width: 100%;
        height: 50px;
    }
}

#left-pane {
    height: 100%;
    border-right: 5px double black; 
    display: grid;
    grid-template-areas: "search"
    "results"
    "addBtn";
}

#search {
    background-color: #2F3233;
    grid-area: search;
}

#results{
    grid-area: results;
}

#addDiv{
    grid-area: addBtn;
    position: relative; 
}

.recipeBtn{
    margin-top: 5px;
    white-space: normal;
    text-overflow: ellipsis;
    border: solid 1px #960015;
    background-color: #2F3233;
    color: #B4BD89;
}

.recipeBtn > p {
    display: inline;
}

#dishText{
    overflow: hidden;
}

.addBtn, .homeCancel{
    position: relative;
    font-size: 1em;
}

.searchInput {
    color: black;
}



@media only screen and (max-width: 768px) {
#left-pane{
    grid-template-rows: 10% 85% 5%;
}

#addDiv{
    width: 100%;
}

.recipeBtn{
    position: relative;
    height: 50px;
    width: 100%;
    margin-top: 5px;
}

.addBtn, .homeCancel{
    height: 100%;
    width: 50%;
}

}

@media only screen and (min-width: 769px){
#left-pane{
    grid-template-rows: 20% 75% 5%;
}

.recipeBtn{
    position: relative;
    height: 50px;
    width: 100%;
    margin-top: 5px;
}

.addBtn, .homeCancel{
    height: 100%;
    width: 50%;
}
}

@media only screen and (min-width: 1000px){
    #left-pane {
        grid-template-rows: 100px 470px 30px;
    }

    .addBtn {
        right: 0;
    }

    .homeCancel, .addBtn {
        height: 30px;
        width: 50%;
    }

    .recipeNames {
        position: relative;
        margin-top: 10px;
    }

    .recipeBtn{
        position: relative;
        height: 50px;
        width: 80%;
        left: 20px;
        font-size: 1em;
    }

    .searchInput{
        width: 95%;
    }
}
#directions,
#ingredients {
    white-space: pre-wrap;
}

#recipeContainer {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-areas: "recipeName""recipeInfo""recipeOutput";
}

#recipeName {
    grid-area: recipeName;
    background-color: #2F3233;
}

.recipeInfo {
    overflow: auto;
    grid-area: recipeInfo;
    display: grid;
    grid-template-areas: "servings"
        "cooking_time"
        "ingredients"
        "directions"
}

.recipeOutput {
    grid-area: recipeOutput;
}

.label {
    font-family: 'Courgette', cursive;
    color: #960015;
}

.servings {
    width: 20%;
}

.value {
    width: 80%;
}

#servings {
    grid-area: servings;

}


#cooking_time {
    grid-area: cooking_time;
    /*     display: grid;
    grid-template-areas: "cooking ctValue"  */
}

#ingredients {
    display: inherit;
    grid-area: ingredients;
    /*     display: grid;
    grid-template-areas: "ingreds ingredsValue"  */
}

#directions {
    display: inherit;
    overflow-x: hidden;
    grid-area: directions;
    /*     display: grid;
    grid-template-areas: "direct directValue"  */
}

#buttons {
    display: grid;
    grid-template-areas: "direct directValue"
}

#search>span {
    text-align: center;
}

#results {
    overflow-y: auto;
    overflow-x: hidden;
}

#edit,
#remove {
    font-size: 1em;
}


@media only screen and (max-width: 768px) {
    #recipeContainer {
        grid-template-rows: 10% 85% 5%;
        overflow-y: auto;
        overflow-x: hidden;
    }


    .recipeInfo {
        grid-template-rows: 5% 5% auto auto;
    }

    .label {
        display: inline-block;
        width: 30%;
        text-align: left;
        font-size: 3.5vw;
        padding-left: 5px;
    }

    .value {
        position: relative;
        display: inline;
        text-align: left;
        font-size: 1em;
    }

    #servings {
        padding-top: 5px;
        grid-template-columns: 20% 80%;
    }

    .recipeOutput {
        margin-top: 10px;
    }
}

@media only screen and (min-width: 768px) {
    #recipeContainer {
        grid-template-rows: 20% 75% 5%;
        overflow-y: auto;
          overflow-x: hidden;
    }

    .recipeInfo {
        grid-template-rows: 5% 5% auto auto;
    }

    .label {
        display: inline-block;
        width: 20%;
        text-align: left;
        font-size: 1.5vw;
        padding-left: 10px;
    }

    .value {
        position: relative;
        display: inline;
        text-align: left;
        font-size: 1em;
        padding-left: 10px;
    }

    #servings {
        position: relative;
        width: 100%;
    }

    #edit,
    #remove {
        font-size: 1em;
        height: 90%;
    }

    .servings {
        width: 20%;
    }


}

@media only screen and (min-width: 1000px) {
    #recipeContainer{
        grid-template-rows: 100px 470px 30px;
        overflow-y: auto;
        overflow-x: hidden;
    }
    
    .recipeInfo{
        grid-template-rows: 60px 60px auto auto;
    }
    
    #servings{
        padding-top: 10px;
        grid-template-columns: 20% 80%;
    }
    
    #cooking_time{
        grid-template-columns: 20% 80%;
    }
    
    #ingredients{
        grid-template-columns: 20% 80%;
    }
    
    #directions{
        grid-template-columns: 20% 80%;
    }

    #buttons{
        grid-template-columns: 50% 50%;
    }

    .label{
        padding-left: 10px;
        font-size: 1.2em;
    }

    .value{
        padding-left: 30px;
        padding-right: 30px;   
    }

    #edit,
    #remove {
        font-size: 1em;
        height: 100%;
    }
}
.editForm{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: "formOutput" "form"   
}

.formOutput{
    grid-area: formOutput;
}

#form{
    grid-area: form;
}

#form > form{
    height: 100%;
    display: grid;
    grid-template-areas: "formHeading" "formServings" "formCooking_time" "formIngredients" "formDirections" "formButtons"
}

#header > h2{
    text-align: center;
}

#formHeading{
    grid-area: formHeading
}

#formServings{
    grid-area: formServings
}

#formCooking_time{
    grid-area: formCooking_time;
}

#formIngredients{
    grid-area: formIngredients
}

#formDirections{
    grid-area: formDirections
}

#formButtons {
    grid-area: formButtons
}

@media only screen and (max-width: 768px) {
.editForm{
    grid-template-rows: 10% 90%;
}

#form > form {
    grid-template-rows: 10% 10% 10% 30% 35% 5%;
}

#save, #cancel{
    width: 50%;
    height: 100%;
}
}

@media only screen and (min-width: 768px){
    .editForm{
        grid-template-rows: 10% 90%;
    }
    
    #form > form {
        grid-template-rows: 10% 10% 10% 30% 35% 5%;
    }

    textarea {
        height: 70%;
    }
    
    #save, #cancel{
        width: 50%;
        height: 100%;
    }
}

@media only screen and (min-width: 1000px){
    .editForm{
        grid-template-rows: 100px 500px;
    }
    
    #form > form {
        grid-template-rows: 50px 50px 50px 160px 160px 30px;
    }

    textarea {
        height: 70%;
    }
    
    #save, #cancel{
        width: 50%;
        height: 100%;
    }
}
